.mouseHover {
    right: 0;
    opacity: 1;
    z-index: -1000;
    top: 0;
    position: fixed;
    filter: none;
    min-width: 100vw;
    min-height: 100vh;
    mix-blend-mode: normal;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -ms-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;

}

.mouseHover2 {
    right: 0;
bottom: -100px;
    opacity: .9;
    z-index: -1000;

    position: fixed;
    filter: none;
    min-width: 100vw;
    min-height: 100vh;
    mix-blend-mode: normal;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -ms-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;

}


.mouseNotHover {
    right: 0;
    z-index: -1000;
    top: 0;
    position: fixed;
    opacity: .1;
    min-width: 100vw;
    min-height: 100vh;
    filter: grayscale(100%) contrast(100%);
    ;
    mix-blend-mode: luminosity;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -ms-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;


}

